/* 首页文章卡片整体布局优化 */
#recent-posts > .recent-post-item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 2rem;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s ease;
  background: var(--anzhiyu-card-bg, #fff);
}

#recent-posts > .recent-post-item:hover {
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
}

/* 封面图样式 */
.post_cover {
  flex: 1 1 40%;
  overflow: hidden;
  border-radius: 0;
  min-height: 180px;
  position: relative;
}

.post_cover img.post_bg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.5s ease;
  border-radius: 0;
}

.post_cover:hover img.post_bg {
  transform: scale(1.05);
}

/* 右侧信息部分 */
.recent-post-info {
  flex: 1 1 60%;
  display: flex !important;
  flex-direction: column;
  padding: 24px 32px !important;
  justify-content: center;
  gap: 6px;
}

.recent-post-info .recent-post-info-top {
  padding: 0 !important;
}

/* 标题 hover 效果 */
.article-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--anzhiyu-text, #333);
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.article-title:hover {
  color: var(--anzhiyu-main, #ff7242);
  text-shadow: 0 1px 3px rgba(255, 114, 66, 0.3);
}

/* 摘要内容 */
#recent-posts > .recent-post-item > .recent-post-info > .content {
  opacity: 1 !important;
  height: auto !important;
  line-height: 1.6 !important;
  margin-top: 0 !important;
  font-size: 15px !important;
  color: var(--anzhiyu-secondtext, #666);
}

/* 顶部 tips（置顶、新标签、分类） */
.recent-post-info-top-tips {
  display: flex;
  gap: 10px;
  position: absolute;
  z-index: 999;
  background: rgb(255 255 255 / 20%);
  padding: 0 5px;
  margin: 10px;
  border-radius: 6px;
  color: var(--anzhiyu-white);
  backdrop-filter: blur(5px);
  font-size: 0.85rem;
}

.recent-post-info-top-tips.right {
  right: 0;
}
.recent-post-info-top-tips.left {
  left: 0;
}

[data-theme='dark'] .recent-post-info-top-tips {
  filter: brightness(0.8);
}

/* 分类、新标签 */
.article-categories-original,
span.newPost {
  display: flex;
  gap: 5px;
  align-items: center;
  color: #ff7242;
}

/* 标签与时间 */
.article-meta-wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  font-size: 13px;
  color: var(--anzhiyu-secondtext);
}

span.article-meta.tags,
span.post-meta-date {
  height: 26px;
  line-height: 26px;
}

/* 隐藏未读提示 */
.unvisited-post {
  display: none !important;
}

/* 响应式支持 */
@media screen and (max-width: 768px) {
  #recent-posts > .recent-post-item {
    flex-direction: column;
  }

  .post_cover,
  .recent-post-info {
    flex: 1 1 100%;
  }

  .recent-post-info-top-tips.right {
    right: inherit;
  }
}
